.nav {
    z-index: 1;
    position: fixed;
    top: 50px;
    left:10px;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding:10px 20px;
    border-radius:5px;
}

@media screen and (max-width:1090px) {
    .nav {
        left: initial;
        top: initial;
        bottom: 0px;
        width: 100%;
    }
}

.nav a {
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}

.nav a:not(:last-child) {
    margin-bottom:20px;
}