.movies .movie {
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.25), 0 -6px 16px -6px rgba(0,0,0,0.0025);
}

.movies .movie a {
    display:grid;
    grid-template-columns: minimax(150px,1fr) 2fr;
    grid: gap 20px;text-decoration: none;
    color:inherit;
}